<script>
import * as echarts from 'echarts';
import {mapMutations, mapState} from "vuex";

const pieData = [

  {name: '中餐厅', value: '12642'},
  {name: '餐饮相关场所', value: '5502'},
  {name: '快餐厅', value: '3544'},
  {name: '糕饼店', value: '1226'},
  {name: '外国餐厅', value: '1074'},
  {name: '咖啡厅', value: '648'},
  {name: '冷饮店', value: '642'},
  {name: '茶艺馆', value: '637'},
  {name: '甜品店', value: '397'},
  {name: '休闲餐饮场所', value: '130'},
  {name: '其他', value: '29'}
];
const rich = {
  name: {
    color: '#666',
    fontSize: 14,
    padding: 7,
    fontWeight: '400',
    align: 'left',
  },
  value: {
    color: '#333',
    fontSize: 15,
    padding: 7,
    fontWeight: '500',
    align: 'left',
  },
  percent: {
    color: '#FFF',
    align: 'right',
    fontSize: 15,
    fontWeight: '500',
  },
  hr: {
    borderColor: '#ccc',
    width: '100%',
    borderWidth: 2,
    height: 0,
  },
  cir: {
    fontSize: 26,
  },
};
var option = {

  tooltip: {
    trigger: 'item',

  },
  legend: {
    orient: 'horizontal',
    left: 'center',
    bottom: 10,
  },
  series: [
    {
      type: 'pie',
      radius: ['20%', '70%'],
      center: ['50%', '40%'],
      label: {
        normal: {
          position: 'inner',
          formatter: (item) => `{percent|${item.percent.toFixed(0)}%}`,
          rich,
        },
      },
      data: pieData,
    },


  ],
};
export default {
  mounted() {
    this.ini();
  },
  computed: {
    ...mapState(['ddomain'])
  },
  methods: {

    ...mapMutations(['gaiddomain']),

    ini() {
      var myChart = echarts.init(this.$refs.chart1);
      myChart.on('click', (params) => {
        const selectedDomain = params.name;

        this.gaiddomain(selectedDomain)
      })

      myChart.setOption(option);
    },
  }
}


</script>

<template>
  <div @click="xx" ref="chart1" style="position: fixed;top:-25px;left:10px;width: 450px;height: 380px"></div>
</template>


