<script >
import * as echarts from "echarts";
import {mapMutations} from "vuex";

var option = {

  tooltip: {
    trigger: 'axis',
    axisPointer: {
      type: 'shadow'
    }
  },

  xAxis: {
    type: 'value',
    boundaryGap: [0, 0.01]
  },
  yAxis: {
    type: 'category',
    data: ['仓山区' ,'台江区' ,'晋安区' ,'闽侯县','马尾区', '鼓楼区' ]
  },
  series: [
    {
      type: 'bar',
      data: [7374, 4891, 6183, 1091, 244, 6688]
    }
  ]
};


export default {
  mounted() {
    this.ini();
  },
  methods: {
    ...mapMutations(['gaidiqu']),
    ini() {
      var myChart = echarts.init(this.$refs.chart3);
      myChart.on('click', (params) => {
        const selectedDomain = params.name;

        this.gaidiqu(selectedDomain)
      })
      myChart.setOption(option
      );
    }
  }
}
</script>

<template>
  <div ref="chart3" style="position: absolute;right: 20px;top: 0;width: 500px;height: 380px"></div>
</template>


