import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    ddomain:'',
    diqu:'',

}

const mutations = {
    gaiddomain(ddd,a) {

        state.ddomain=a

    },
    gaidiqu(ddd,a) {

        state.diqu=a

    },
}

const actions = {

}

export default new Vuex.Store({
    state,
    actions,
    mutations,
})
